  /* Skills.css */

body {
  background-color: #C663A4; /* Cornflower Blue */
}

h2 {
  font-size: xx-large;
  margin-bottom: 30px;
}

/* Import Slick Carousel CSS */
@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

/* Custom styles for the Skill component */
.skills-container {
  width: 90%;
  margin: auto;
  margin-top: 5rem;
}

.skill-card {
  background-color: pink;
  height: 550px;
  font: black;
  border-radius: 1rem;
  /* display: flex; */
  
  /* flex-direction: column; */
  /* justify-content: space-between; */
  opacity: 0.72; /* Default reduced visibility */
  transition: opacity 0.3s ease; /* Smooth transition */
}

.skill-card:hover {
  opacity: 1; /* Full visibility on hover */
}

.skill-card:hover ~ .skill-card {
  opacity: 0.6; /* Reduce visibility of non-hovered cards */
}

.skill-image-container {
  height: 46%;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;

  background-color: #FDC435; /* Indigo-500 */
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.skill-image {
  height: 11rem;
  width: 11rem;
  border-radius: 50%;
  margin-left: 1rem;
  margin-bottom: 5rem;
}

.skill-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
}

.skill-title {
  font-size: 1.25rem; 
  font-weight: 600; 
}

.slick-slide > div {
  margin: 0 10px;
}

.Projects {
  font-size: 1.20rem;
  white-space: pre-line; /* Preserve new lines in text */
  text-align: left; /* Optional: to align text to the left */
}